"use client";
import scss from "./OlympaidsSelection.module.scss";
import React from "react";
import { useRouter } from "next/navigation";
import { useGetOlympiansSelectionQuery } from "@/redux/api/olympians";

const OlympaidsSelection = () => {
    const router = useRouter();
    const { data, isError, isLoading } = useGetOlympiansSelectionQuery();

    if (isLoading) return <div className={scss.loading}>Загрузка...</div>;
    if (isError || !data) {
        return <div className={scss.error}>Ошибка при загрузке данных.</div>;
    }

    console.log(data);

    return (
        <div className={scss.OlympaidsSelection}>
            <div className="container">
                <div className={scss.content}>
                    <h1 className={scss.title}>Виды олимпиад</h1>
                    <div className={scss.cards}>
                        {data.map((category) => (
                            <div
                                onClick={() =>
                                    router.push(
                                        `/olymp_categories/${category.id}`
                                    )
                                }
                                key={category.id}
                                className={scss.card}
                            >
                                <h2>{category.name_of_olympia}</h2>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default OlympaidsSelection;
